.bannerPortfolio {
  color: #fff;
  width: 100%;
  height: 200px;
  background-image: url('../../images/banner.jpg');
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container{
    width: 100%;
}
.card{
    background-color: #bfc7cf;
}
.card2 {
    background-color: #eaecee;
   
}
.portfolio-wrapper{
    background-color: #427CAC;
    margin: auto;
    display:flex;
    flex-wrap: wrap;    
}
.portfolio-wrapper a {
text-decoration:none
}
.portfolio-wrapper a:link, a:visited { text-decoration: none }
.portfolio-card {
   padding: 10px;
   text-align: center;
   margin: auto;
   width: 100%;
   height: 370px;
   margin-top: 10px;
}
.portfolio-title{
    color: #0092D1;
    padding: 10px;
}
.portfolio-img img{
    width: 90%;
    border: black 0.5px solid;
}
.portfolio-p{
   margin: auto;
    width: 80%;
    padding: 5px;
}

@media screen and (min-width: 768px){
    .portfolio-card{
        width: 30%; 
        height: 350px;
        margin:auto;
        display: block; 
        border-radius: 15px;   
      }
      .portfolio-title{
        font-size: 15px;
      }
      .portfolio-p{
        font-size: 14px;
        
      }

}
@media screen and (min-width: 992px){

  .portfolio-wrapper{
  
    display: flex;
  } 
    .portfolio-card{
      width: 45%; 
      height: 430px;
      margin:auto;
      margin-top: 10px;
      display: block; 
      border-radius: 15px;   
    }
    .portfolio-img{
        width: 100%
    }
}